export default function localizedNumberText(value, language) {
  let result = ''
  let tobbProcessed = parseFloat(value)
  language = language.toLowerCase()

  if (tobbProcessed) {
    if (language === 'bd') {
      result = tobbProcessed.toLocaleString('bn-BD')
    } else {
      //you may add more future language support here
      return value
    }
  } else {
    return value
  }

  return result
}
